@import url(https://fonts.googleapis.com/css?family=Playfair+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  height: auto;
  border-radius: 15px;
  width: 200px;
}

img.lg {
  width: 1000px;
  margin: 2rem auto;
}

img.md {
  width: 800px;
  margin: 1.5rem auto;
}

img.base {
  width: 400px;
}

img.sm {
  width: 200px;
}

.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
}

.description {
  margin: 2rem;
}
.full-page {
  height: 100vh;
  width: 100vw;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Playfair Display", serif;
}

h1 {
  padding: 3rem 0;
  text-align: center;
}

h2 {
  margin-bottom: 1rem;
}

.header-image-container {
  display: flex;
  margin: auto;
}

.header-image-container img {
  margin: auto;
  width: 100%;
  height: auto;
  max-height: 30vh;
  object-fit: cover;
  border-radius: 0;
}

body {
  height: 100%;
}

#home {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  background-image: url(/static/react/static/media/austin.5df00a1d.jpg);
  background-size: cover;
}

.title {
  color: White;
  background-color: rgba(0,0,0,0.8);
  padding-left: 10rem;
  padding-right: 10rem;
  margin: auto;
  margin-top: 0;
  margin-bottom: 5rem;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}

.bottom-text-container {
  color: White;
  background-color: rgba(0,0,0,0.8);
  border-radius: 8px;
}
.city-name {
    font-size: 1.5rem;
}

.flex-links {
  list-style-type: none;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.card-img-top {
  border-radius: 0;
}

.about .card {
  height: 100%;
  color: black;
}


.cities-list {
  justify-content: center;
  padding: 25px;
  margin: auto;
}
.city-listing {
  margin: 15px;
}

.image{
  height: 200px;
}

.cities-list td {
  max-width: 400px;
  min-width: 150px;
}

.cities-list th {
  max-width: 400px;
  min-width: 150px;
}

.react-bs-table .table {
  margin-bottom: 0;
  table-layout: fixed;
}

td.fit-content {
  width: auto;
}
.sort-column:hover {
  cursor: pointer;
}

.pagination {
  margin-bottom: 0;
}

.react-tagsinput {
  background-color: #fff;
  border: 1px solid #ccc;
  margin-right: 1rem;
  border-radius: 0.25rem;
  overflow: hidden;
  flex-grow: 1;  
}

.react-tagsinput--focused {
  box-shadow: 0 0 0 0.1rem rgba(0,123,255,.3)
}

.react-tagsinput-tag {
  border-radius: 0.25rem;
  border: 1px solid #005cbf;
  color: #005cbf;
  display: inline-block;
  margin-bottom: 5px;
  margin-right: 5px;
  padding: 5px;
}

.react-tagsinput-remove {
  cursor: pointer;
  font-weight: bold;
}

.react-tagsinput-tag a::before {
  content: " ×";
}

.react-tagsinput-input {
  background: transparent;
  width: 100%;
  border: 0;
  color: #777;
  font-size: 14px;
  font-weight: 400;
  margin: auto;
  outline: none;
  padding: 8px;
}

.react-autosuggest__suggestions-container {
  z-index: 100;
  max-height: 10rem;
  overflow-y: scroll;
}
.react-autosuggest__suggestions-container ul {
  list-style: none;
}

.react-autosuggest__suggestions-container li {
  cursor: pointer;
}
.table-actions {
  margin: 1rem 0;
}

.table-actions > div {
  margin: 0.5rem 0;
}

.event-info {
    padding: 30px;
    text-align: center;
    display: flex;
    justify-content: space-around;
}
.event-content {
    border-radius: 25px;
    padding: 30px;
    flex-wrap: wrap;
    display: inline-block;
    background-color: rgba(128, 0, 0, 0.685);
}

.event-content p {
    text-align: center;
    color: white;
}

.event-image {
    display: inline-block;
}
.event-image img {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: auto;
    max-height: 300px;
}
.hashtag {
    color: wheat;
    font-family: Impact;
}

.hyperlink {
    color: peachpuff;
}

.form-inline > * {
  margin-left: 1rem;
  margin-right: 1rem;
}

.custom-control .custom-control-label {
  justify-content: start;
}

#search-bar {
  width: 50%;
}

.dropdown-menu.show {
  padding: 1rem;
}
